@import "src/theme/colors";

.Status {
  padding-left: 14px;
  position: relative;
  text-transform: capitalize;

  > i {
    border-radius: 50%;
    display: block;
    height: 7px;
    left: 0;
    position: absolute;
    top: 5px;
    width: 7px;
  }

  &.active {
    > i {
      background-color: $green;
    }
  }

  &.paused {
    > i {
      background-color: $yellow;
    }
  }

  &.blocked {
    > i {
      background-color: $red;
    }
  }

  &.invited {
    > i {
      background-color: $blue;
    }
  }
}
