@import "background";
@import "colors";
@import "font";

.ui.checkbox.radio {
  line-height: 1.25rem;
  min-height: 1.25rem;
  min-width: 1.25rem;

  label {
    font-size: 1rem;
    line-height: 1.5rem;
    padding-left: 2rem;

    &:before {
      background-color: transparent;
      border-color: $white;
      height: 1.25rem;
      opacity: 0.5;
      width: 1.25rem;
    }

    &:after {
      background-color: $link-color;
    }

    &:active,
    &:focus,
    &:hover {
      &:before {
        background-color: transparent;
        border-color: $blue;
        opacity: 1;
      }
    }
  }

  input {
    &:active,
    &:focus,
    &:hover {
      & ~ label {
        &:before {
          background-color: transparent;
          border-color: $blue;
        }

        &:after {
          background-color: $link-color;
        }
      }
    }

    &:checked {
      & ~ label {
        color: $light-grey;

        &:before {
          background-color: transparent;
          border-color: $blue;
          opacity: 1;
        }

        &:after {
          background: $link-color;
          box-shadow: 1px 1px 5px 2px rgba(31, 142, 250, 0.21), inset 0 4px 10px #013F7C;
          height: 1.6rem;
          left: -2px;
          opacity: 1;
          top: -2px;
          width: 1.6rem;
        }
      }

      &:active,
      &:focus,
      &:hover {
        & ~ label {
          &:before {
            background-color: transparent;
            border-color: $blue;
            opacity: 1;
          }

          &:after {
            background-color: $link-color;
          }
        }
      }
    }
  }
}
