@import "background";
@import "font";

.ui.toggle.checkbox {
  min-height: 0.625rem;
  top: -0.3125rem;
  width: 1.875rem;

  & > label {
    min-height: 0.625rem;
    min-width: 1.875rem;

    &:before {
      background: $dark-background-darken;
      width: 1.875rem;
      height: 0.625rem;
    }

    &:after {
      background: $dark-main-color;
      height: 0.9375rem;
      top: -0.125rem;
      width: 0.9375rem;
    }
  }

  input {
    &:checked {
      & ~ label {
        &:before {
          background-color: $dark-background-darken !important;
        }

        &:after {
          background: $link-color;
          left: 1.25rem;
        }
      }
    }

    &:active,
    &:focus {
      &:checked {
        & ~ label {
          &:before {
            background-color: $dark-background-darken !important;
          }
        }
      }
    }
  }
}
