@import "src/theme/colors";

.StatementsHeader {
  display: flex;

  > div {
    flex: 4;

    .ui.dropdown {
      .text {
        text-transform: capitalize;
      }
    }

    &:last-child {
      flex: 20;
      text-align: right;

      .Export {
        > button {
          line-height: 1rem;
          padding-left: 0.1rem;
          padding-right: 0.1rem;
          width: 7rem;

          > svg {
            font-size: 1.1rem;
            margin-right: 0.5rem;
            position: relative;
            top: 2px;
          }
        }

        > i {
          display: none !important;
        }

        > .menu {
          width: 7rem;

          .item {
            color: $light-grey-text;

            &:hover {
              color: $white;
            }

            > svg {
              position: relative;
              top: 0.5rem !important;
              width: 1.3rem;
            }
          }
        }
      }
    }
  }
}
