@import "src/theme/background";
@import "src/theme/colors";
@import "src/theme/font";

.Layout {
  height: 100%;
  padding: 0 !important;

  &.dark {
    color: $dark-main-color !important;
    background-color: $dark-main !important;
    min-height: 100vh;
  }

  > .Wrapper {
    display: flex;

    nav {
      flex: 2;
    }

    main {
      flex: 10;
      min-height: 100vh;
      padding: 1.8rem 2.5rem 0 0;

      > .Holder {
        background: linear-gradient(295.22deg, $dark-main-blue 13.81%, rgba(26, 39, 66, 0.9) 102.47%);
        box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.15);
        border-radius: 0.625rem;
        padding: 2.1875rem 1.25rem;

      }
    }
  }
}
