@import "colors";

.react-datepicker {
  background-color: $dark-main-lighten;
  border: none;
  border-radius: 0;

  &__navigation-icon::before,
  &__year-read-view--down-arrow,
  &__month-read-view--down-arrow,
  &__month-year-read-view--down-arrow {
    border-color: $light-grey-text;
    border-width: 1px 1px 0 0 ;
    height: 5px;
    top: 12px;
    width: 5px;
  }

  &__header {
    background-color: $dark-main-lighten;
    border: none;
    padding: 12px 0 2px;
  }

  &__current-month {
    color: $light-grey;
    font-size: 0.825rem;
    font-weight: 300;
    margin-bottom: 6px;
  }

  &__day-name {
    color: $light-grey-text;
  }

  &__day {
    background-color: rgba(58, 70, 95, 0.6);
    border: 1px solid rgba(58, 70, 95, 0.6);
    color: $light-grey;

    &:hover {
      background-color: $grey-blue-hover;
      border-color: $blue;
    }

    &--outside-month {
      background-color: transparent;
      color: $light-grey-text;
    }

    &--in-range {
      background-color: rgba(97, 177, 255, 0.4) !important;
      color: $light-grey !important;

      &.react-datepicker__day--outside-month {
        background-color: transparent !important;
        color: $light-grey-text !important;
      }
    }

    &--selected {
      background-color: $blue !important;
    }
  }

  &__triangle {
    &:after {
      border-bottom-color: $dark-main-lighten !important;
    }

    &:before {
      border-bottom-color: $dark-main-lighten !important;
    }
  }
}
