@import "src/theme/background";
@import "src/theme/colors";
@import "src/theme/font";

.Analytics {
  > header {
    display: flex;

    .ui.mini.buttons {
      margin-left: auto;

      button {
        color: $light-grey-blue;
        font-size: 1rem;
        height: 2.5rem;
        min-height: 0;
        padding: 0.625rem 3.375rem;

        &.primary {
          color: $white;
        }

        > svg {
          margin-right: 0.5rem;
          position: relative;
          top: 2px;
        }
      }
    }
  }

  .ui.segment {
    background: $dark-card-black !important;
    padding: 1.875rem;

    h3 {
      font-size: 1.875rem;
      font-weight: 500 !important;
      margin-bottom: 2.5rem;
    }

    .Holder {
      display: flex;
      gap: 3rem;

      > div {
        flex: 1;

        > section {
          display: flex;
          gap: 2rem;

          > div {
            flex: 1;

            &:first-child {
              border-right: 1px dashed #fff3;
              padding-right: 2rem;
            }

            h4 {
              font-size: 1.375rem;
              font-weight: 500 !important;
            }

            dl {
              display: flex;
              margin: 0.5rem 0;

              dt {
                color: $light-grey-text;
                width: 60%;
              }

              dd {
                color: $dark-lead-color;
                text-align: right;
                width: 40%;
              }
            }
          }
        }

        .Total {
          color: $dark-lead-color;
          font-size: 1.375rem;
          font-weight: 500;
          margin-top: 3rem;

          > span {
            margin-right: 43%;
          }
        }
      }
    }
  }
}
