@import "font";

.ui.form {
  .field,
  .field.inline {
    label {
      color: $dark-dark-color;
    }

    > label {
      color: $dark-lead-color;
    }
  }

  .field {
    .ui.labeled.input {
      position: relative;

      .ui.label.label {
        background-color: transparent;
        color: $placeholder-color !important;
        font-size: 1.125rem !important;
        font-weight: 300;
        left: 0;
        opacity: 0.5;
        position: absolute;
        top: 0.5rem;
        transition: all 0.2s ease;
        z-index: 1;
      }

      input {
        z-index: 2;

        &:focus {
          & + .ui.label.label {
            font-size: 0.8rem !important;
            left: 0.3rem;
            opacity: 0.7;
            top: -0.15rem;
          }
        }
      }

      &.filled {
        input {
          & + .ui.label.label {
            font-size: 0.8rem !important;
            left: 0.3rem;
            opacity: 0.7;
            top: -0.15rem;
          }
        }
      }
    }
  }
}
