@import 'src/theme/colors';

.Balance {
  display: flex;
  gap: 1.5rem;
  padding-bottom: 1rem;

  .Main {
    display: flex;
    gap: 0.8rem;

    .Value {
      color: $white;
      font-size: 1.875rem;
      font-weight: 700;

      strong {
        display: block;
        font-size: 1.125rem;
        font-weight: 400;
        margin-bottom: 0.5rem;
        opacity: 0.7;
      }
    }

    .Button {
      padding-top: 0.83rem;
    }
  }

  .Stats {
    padding-top: 0.3rem;

    p {
      font-size: 1.125rem;
      margin-bottom: 0.1rem;

      strong {
        color: $white;
        font-weight: 400;
        margin-left: 0.3rem;
      }

      button.button {
        background-color: transparent !important;
        color: $light-grey-blue !important;
        font-size: 1rem;
        margin-left: 0.8rem;
        padding: 0;
        position: relative;
        top: 0.08rem;

        &:active,
        &:focus {
          background-color: transparent !important;
          color: $light-grey-blue !important;
        }

        &:hover {
          background-color: transparent !important;
          color: lighten($light-grey-blue, 10%) !important;
        }
      }
    }
  }
}
