@import "src/theme/colors";

.TopUpModal {
  .content {
    display: flex !important;

    > div {
      flex: 1;

      &:first-child {
        border-right: 2px solid #303A50;
        padding-right: 3rem;
      }

      &:last-child {
        padding-left: 3rem;

        p {
          line-height: 2rem;
        }
      }

      h4 {
        font-weight: 500 !important;
      }

      p {
        color: rgba(249, 249, 249, 0.7);
        font-size: 1.125rem;
        margin-bottom: 0.625rem;
      }
    }
  }

  .actions {
    position: relative;

    > span {
      color: $blue;
      cursor: pointer;
      font-size: 1.125rem;
      left: 2.5rem;
      position: absolute;
      top: 1rem;
    }
  }
}
