@import "src/theme/background";
@import "src/theme/colors";
@import "src/theme/font";

.TopUpsList {
  .Header {
    color: rgba(249, 249, 249, 0.7) !important;
    font-size: 1.5rem !important;
    font-weight: 300 !important;
    padding: 0 !important;
    text-align: left;
  }

  > ul {
    margin: 0;
    padding: 0;

    > li {
      background-color: $dark-main;
      display: block;
      font-size: 1rem;
      font-weight: 300;
      margin-top: 0.625rem;
      padding: 0.75rem 1rem;

      &:first-child {
        margin: 0;
      }

      > span {
        margin-right: 4rem;

        &.Status {
          padding-left: 0;

          > svg {
            font-size: 1.2rem;
            position: relative;
            top: 0.2rem;
          }
        }

        &.Amount {
          color: $dark-lead-color;
        }

        &.Company {
          color: $dark-main-color;
        }
      }
    }
  }
}
