@import 'src/theme/colors';

.Company {
  margin-bottom: 1.875rem;
  text-align: center;

  > img {
    border: 2px solid $blue;
    border-radius: 50%;
    display: block;
    height: 5.625rem;
    margin: 0 auto 1rem;
    overflow: hidden;
    width: 5.625rem;
  }

  .dropdown {
    font-size: 1rem;
    font-weight: 300;

    > svg {
      color: $blue;
      margin-left: 4px;
      position: relative;
      top: 4px;
    }

    > .menu {
      background-color: $dark-blue-darken;

      > .item {
        color: $light-grey-blue;

        &:hover {
          color: lighten($light-grey-blue, 10%)
        }
      }
    }
  }

  .Name {
    color: $white;
    font-size: 1.5rem;
    font-weight: 300;
  }

  .Address {
    color: $white;
    font-size: 0.875rem;
    margin-top: 0.5rem;
  }
}
