@import 'src/theme/background';
@import 'src/theme/colors';
@import 'src/theme/font';

.Card {
  background-color: $dark-card-black;
  border-radius: 0.625rem;

  .Title {
    color: rgba(249, 249, 249, 0.7) !important;
    font-size: 1.125rem !important;
    font-weight: 300 !important;
    padding: 0.9rem 0 0 !important;
    position: relative;
    text-align: center;

    &.big {
      font-size: 1.5rem !important;
    }

    > span {
      color: $light-grey;
      display: block;
      font-weight: 500;
      left: 0;
      position: absolute;
      text-align: center;
      top: 2.3rem;
      width: 100%;
    }
  }

  .Content {
    align-items: center;
    display: flex;
    justify-content: center;
    min-height: 8.2rem;
  }

  .Link {
    display: flex;
    justify-content: center;
    padding: 1.5rem 0;

    a {
      color: $blue;
    }
  }
}
