@import 'background';
@import 'colors';
@import 'font';

table.ui.table {
  background: $dark-blue-table;
  border: 0;
  border-radius: 2px;
  color: $dark-dark-color;

  svg {
    color: $dark-dark-color;
  }

  thead {
    th {
      background: $dark-blue-table;
      border-bottom: 0.3125rem solid $dark-card-black;
      color: $dark-lead-color;
      font-size: 1.125rem;
      font-weight: 400;
      padding: 1.4rem 1.875rem;
    }
  }

  tbody {
    tr {
      td {
        border-top: 3px solid $dark-card-black;
        padding: 1rem 1.875rem;
      }
    }
  }
}
