@import "src/theme/background";
@import "src/theme/font";

.Sign {
  background-color: $dark-background !important;
  background-image: url('./imgs/bg2.png') !important;
  background-position: left top !important;
  background-repeat: no-repeat !important;
  color: $dark-main-color;
  display: flex;
  min-height: 100vh;
  padding: 0 !important;

  .Left {
    flex: 6;
    padding: 4.375rem;
    position: relative;

    > h1 {
      color: $dark-lead-color;
      font-size: 3rem !important;
      font-weight: 900 !important;
    }

    > p {
      bottom: 4.375rem;
      color: $dark-lead-color;
      font-size: 2rem;
      font-weight: 300;
      left: 4.375rem;
      position: absolute;

      > strong {
        display: block;
        font-weight: 700;
      }
    }
  }

  .Right {
    background: $dark-background;
    flex: 4;
    min-height: 100vh;
    width: 40%;

    > .segment {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      margin: 0 auto;
      min-height: 100vh;
      width: 80%;

      .Form {
        h1 {
          color: $dark-lead-color;
          font-size: 1.875rem !important;
          font-weight: 500 !important;
          margin-bottom: 2rem;
          text-align: center;
        }

        .ui.form {
          .field {
            margin-bottom: 1.25rem;

            &.ButtonHolder {
              margin-top: 2.5rem;
            }
          }

          .ui.right.labeled.input.filled.phone {
            height: 3.825rem;
            position: relative;

            .PhoneInput {
              .PhoneInputCountryIcon {
                margin-left: 0.7rem
              }

              .PhoneInputCountrySelectArrow,
              select {
                display: none;
              }
            }

            input[type=tel] {
              left: 0;
              padding-left: 3rem;
              position: absolute;
              top: 0;
              width: 100%;
            }

            .ui.label.label {
              font-size: 0.8rem !important;
              left: 2.4rem !important;
              opacity: 0.7 !important;
              top: -0.15rem !important;
            }
          }
        }

        .Lead {
          color: $grey-blue;
          font-weight: 300;
          margin: 0 auto 3.125rem;
          text-align: center;
          width: 100%;
        }

        .Forget {
          font-size: 1.125rem;
          margin-top: 1.875rem;
          text-align: center;

          > a {
            color: $grey-blue;
          }
        }
      }
    }

    section:last-child {
      color: $grey-blue;
      font-size: 1.125rem;
      padding-bottom: 1.25rem;
      text-align: center;

      a {
        font-weight: 700;
        margin-left: 0.625rem;
      }
    }
  }
}
