@import "background";
@import "colors";
@import "font";

.ui.input {
  background-color: $dark-background-block;
  border-radius: 0.5rem !important;
  color: $dark-dark-color;

  &.error {
    input {
      &[type=text],
      &[type=email],
      &[type=number],
      &[type=tel],
      &[type=search],
      &[type=password] {
        border: 1px solid $red !important;
      }
    }
  }

  input {
    &[type=text],
    &[type=email],
    &[type=number],
    &[type=tel],
    &[type=search],
    &[type=password] {
      background: transparent;
      border-radius: 0.5rem !important;
      color: $light-grey;
      font-size: 1.125rem;
      font-weight: 300;
      padding: 1.4rem 0.9rem 0.9rem 0.9rem;

      &:active,
      &:focus {
        background: transparent;
        border-color: $blue;
        box-shadow: none;
        color: $light-grey;
      }

      &::selection{
        background: $link-color;
        color: $dark-lead-color;
      }

      &::-moz-selection{
        background: $link-color;
        color: $dark-lead-color;
      }
    }

    &[type=number] {
      &::-webkit-outer-spin-button,
      &::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
    }
  }

  &.left {
    > svg {
      color: $white;
      height: auto;
      left: 0.6rem;
      opacity: 0.5;
      position: absolute;
      top: 0.6rem;
      width: 0.9rem;
    }
  }

  &:not(.left) {
    > svg {
      color: $white;
      cursor: pointer;
      position: absolute;
      line-height: 1;
      text-align: center;
      top: 0;
      right: 1.25rem;
      margin: 0;
      height: 100%;
      width: 1.25rem;
      opacity: .5;
      border-radius: 0 .28571429rem .28571429rem 0;
      transition: opacity .3s ease;

      &:hover {
        color: $dark-lead-color;
      }
    }
  }
}

::placeholder {
  color: $placeholder-color !important;
  font-size: 1.125rem !important;
  font-weight: 300 !important;
  opacity: 0.5;
}
