@import "background";
@import "colors";
@import "font";

.ui.dimmer {
  background-color: rgba(47, 59, 82, 0.6);
}

.ui.modal {
  background: $dark-card-black;

  > svg {
    color: rgba(255, 255, 255, 0.4);
    cursor: pointer;
    font-size: 1.5rem;
    position: absolute;
    right: 1.875rem;
    top: 1.875rem;
  }

  > .header,
  > .content,
  > .actions {
    background: $dark-card-black;
    color: $dark-dark-color;
    padding: 1.875rem 2.5rem;
  }

  > .header {
    color: rgba(249, 249, 249, 0.7);
    font-size: 1.5rem;
    font-weight: 300 !important;
  }

  .Actions {
    margin-top: 1.5625rem;
    text-align: center;
  }
}
