@import 'background';
@import "colors";
@import 'font';

.ui.checkbox:not(.radio):not(.toggle) {
  line-height: 1rem;
  min-height: 1rem;
  min-width: 1rem;

  label {
    &:before {
      background: $dark-background-lighten;
      border-color: $dark-main;
      height: 1rem;
      width: 1rem;
    }

    &:active,
    &:focus,
    &:hover {
      color: $grey-blue-lighten;

      &:before {
        background: lighten($dark-background-lighten, 10%);
        border-color: $dark-main;
      }
    }
  }

  input {
    &:active,
    &:focus,
    &:hover {
      & ~ label {
        color: $grey-blue-lighten;

        &:before {
          background: lighten($dark-background-lighten, 10%);
          border-color: $dark-main;
        }
      }
    }

    &:checked {
      & ~ label {
        &:before {
          background: lighten($dark-background-lighten, 10%);
          border-color: $dark-main;
        }

        &:after {
          background: $link-color;
          border-radius: 1px;
          content: '';
          height: 0.9rem;
          left: 1px;
          opacity: 1;
          top: 1px;
          width: 0.9rem;
        }
      }

      &:active,
      &:focus,
      &:hover {
        & ~ label {
          &:before {
            background: lighten($dark-background-lighten, 10%);
            border-color: $dark-main;
          }
        }
      }
    }
  }
}
