@import "src/theme/colors";
@import "src/theme/font";

.Notifications {
  ul {
    margin: 0;
    padding: 0;

    li {
      display: block;
      margin-bottom: 10px;
      padding-left: 25px;
      position: relative;

      &:last-child {
        margin-bottom: 0;
      }

      svg {
        left: 0;
        position: absolute;
        top: 1px;
      }

      p {
        font-size: 12px;
        margin-bottom: 0;

        &.Date {
          color: $dark-main-color;
          font-size: 10px;
          font-weight: 900;
        }
      }
    }
  }

  > p {
    margin: 10px 0;
    text-align: right;

    > span {
      color: $link-color;
      cursor: pointer;

      &:hover {
        color: lighten($link-color, 10%);
      }
    }
  }
}
