@import "src/theme/colors";

.AddCardForm {
  .inline.field {
    display: flex;
    justify-content: center;
    line-height: 2rem;
    margin-bottom: 1.5rem;

    > label {
      color: #8B919A !important;
      font-size: 1rem !important;
      font-weight: 500 !important;
      line-height: 4rem;
      position: relative;
      top: -1px;
      width: 10rem !important;
    }

    > div {
      line-height: 4rem;
      width: 15rem;
    }

    .checkbox {
      margin-right: 1.5rem;
    }

    select,
    .ui.input {
      background-color: $dark-blue-darkest !important;
      width: 15rem !important;

      &.error {
        background-color: #3c0f3e !important;
      }
    }
  }

  .Actions {
    padding: 1.5rem 0;
    text-align: right !important;
  }
}
