@import "background";
@import "colors";
@import "font";

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  background: $dark-background-block !important;
  border-radius: 0.5rem !important;
  color: $light-grey !important;
  font-size: 1.125rem;
  font-weight: 300;
  padding: 1.2rem 0.8rem !important;

  &:active,
  &:focus {
    background: $dark-background-block;
    border: 1px solid $blue !important;
    box-shadow: none;
    color: $light-grey;
    outline: none !important;
  }
}
