@import "src/theme/colors";

.Cards {
  header {
    margin-bottom: 1.5rem;

    a {
      color: $light-grey-blue;
      cursor: pointer;
      font-size: 1.5rem !important;
      font-weight: 300;
      line-height: 3rem;
      margin-right: 1.5rem;
      padding-bottom: 0.5rem;

      &.active {
        border-bottom: 2px solid $white;
        cursor: default;
        color: $white;
        font-weight: 400;
      }
    }
  }

  .TableHeading {
    display: flex;

    .ui.dropdown {
      margin-top: 10px;
    }

    .ui.input {
      margin-left: 1rem;
      position: relative;
      top: -1px;
      width: 20rem;

      input {
        background: $dark-card-black;
        font-size: 0.875rem;
        padding: 0.5rem;

        &::placeholder {
          font-size: 0.875rem !important;
        }
      }
    }

    .Sorting {
      margin-left: auto;

      > button {
        margin-left: 15px;
        position: relative;
        top: -3px;
      }
    }
  }
}
