@import 'src/theme/colors';
@import 'src/theme/font';

.Dashboard {
  .Wrapper {
    align-items: stretch;
    display: flex;
    gap: 0.8rem;
    margin-top: 10px;

    .Chart {
      flex: 8;

      .ChartHolder {
        background: $dark-card-black;
        border-radius: 0.625rem;

        header {
          display: flex;
          padding: 0.9375rem 1.5rem;

          h3 {
            color: rgba(249, 249, 249, 0.7) !important;
            font-size: 1.5rem !important;
            font-weight: 300 !important;
            position: relative;
            top: 0.3125rem;
          }

          .ui.buttons {
            margin-left: auto;

            .ui.button {
              color: rgba(249, 249, 249, 0.4);
              padding: 0.625rem 0.8125rem;

              &.active {
                background-color: #202b41;
                color: #f9f9f9;
              }
            }
          }
        }
      }
    }

    .Holder {
      display: flex;
      flex: 2;
      flex-direction: column;
      gap: 1rem;

      &.Pie {
        .Card {
          padding-bottom: 7px;
        }

        canvas {
          max-height: 17rem;
          max-width: 17rem;
        }
      }
    }

    .List {
      flex: 6;

      .TopUpsList {
        padding: 1.1875rem 3.5rem;
      }

      ul {
        max-height: 15.5rem;
        overflow: hidden;
        overflow-y: auto;
      }
    }

    .Card {
      .Number {
        color: $dark-lead-color;
        font-size: 4rem;
        font-weight: 700;

        &.thin {
          font-weight: 400;
        }
      }

      .Days {
        display: flex;
        position: relative;

        > div {
          &:first-child {
            color: $dark-lead-color;
            font-size: 4rem;
            font-weight: 400;
          }

          &:last-child {
            font-size: 1rem;
            font-weight: 300;
            position: relative;
            text-transform: uppercase;
            top: 1.1rem;
          }
        }
      }

      &.Avg {
        .Content {
          flex-direction: column;

          .Number {
            font-size: 3rem;
          }
        }

        .Enough {
          color: $light-grey !important;
          font-size: 1rem !important;
          font-style: normal;
          font-weight: 300;
          position: relative;
          top: 1rem;
        }
      }
    }
  }
}
