@import "background";
@import "colors";

.ui.popup {
  background: $dark-background-block;
  border-color: $dark-background-darken;
  color: $light-grey-blue;
  padding: 1.2rem 1.8rem;

  &:before {
    background: $dark-background-block !important;
    box-shadow: none !important;
  }

  &.CalendarPopup {
    background-color: $dark-main-lighten;
    padding: 0.5rem;

    &:before {
      background: $dark-main-lighten !important;
    }
  }
}
