@import 'src/theme/background';
@import 'src/theme/colors';
@import 'src/theme/font';

.Header {
  text-align: right;

  .Actions {
    .BellHolder {
      position: relative;

      &.new {
        &:after {
          background-color: $blue;
          border-radius: 50%;
          content: '';
          display: block;
          height: 0.6875rem;
          position: absolute;
          right: 0.34rem;
          top: -0.34rem;
          width: 0.6875rem;
        }
      }
    }

    a.button,
    button.button {
      background-color: transparent !important;
      color: $dark-main-color !important;
      font-size: 1.4rem;
      margin-left: 0.7rem;
      padding: 0;

      &:active,
      &:focus {
        background-color: transparent !important;
        color: $dark-main-color !important;
      }

      &:hover {
        background-color: transparent !important;
        color: lighten($dark-main-color, 10%) !important;
      }
    }

    .dropdown {
      margin-left: 0.7rem;
      position: relative;
      top: -1px;
    }
  }
}

.NotificationsModal {
  ul {
    margin: 0;
    padding: 0;

    li {
      display: block;
      margin: 1rem 0 0;
      padding-left: 2.5rem;
      position: relative;

      svg {
        font-size: 1.25rem;
        left: 0;
        position: absolute;
        top: 0.3125rem;
      }

      p {
        color: $dark-lead-color;
        font-size: 1rem;
        margin-bottom: 0;

        &.Date {
          color: $dark-main-color;
          font-size: 0.875rem;
          font-weight: 300;
        }
      }
    }
  }
}
