@import "background";
@import "font";

.ui.button {
  background: $dark-background-darken;
  border-radius: 0.3125rem;
  color: $dark-lead-color;
  font-size: 0.875rem;
  font-weight: 400;
  padding: 0.8rem 3.4275rem;

  &:active,
  &:focus {
    background: $dark-background-darken;
    color: $dark-lead-color;
  }

  &:hover {
    background: lighten($dark-background-darken, 5%);
    color: $dark-lead-color;
  }

  &.primary {
    background: $blue;

    &:hover {
      background: $blue-hover;
    }

    &:focus {
      background: $blue;
    }

    &:active {
      background: $blue-active;
    }
  }
}
