@import 'background';
@import "font";

.ui.dropdown {
  > .divider.text {
    position: relative;
    top: -3px;
  }

  > svg {
    font-size: 0.8rem;
    margin-left: 0.8rem;
  }

  > .menu {
    background-color: $dark-background-block;

    > .item {
      color: $dark-main-color;

      &:hover {
        color: lighten($dark-main-color, 10%)
      }

      > svg {
        margin-right: 7px;
        position: relative;
        top: 2px;
      }
    }
  }
}
