@import 'src/theme/background';
@import 'src/theme/colors';

.Sidebar {
  padding-top: 2.5rem;

  h2 {
    color: $white;
    font-size: 2.5rem !important;
    font-weight: 900 !important;
    margin-bottom: 2.5rem !important;
    text-align: center;
  }

  ul {
    margin: 0;
    padding: 0;

    li {
      display: block;
      font-size: 1.125rem;

      a {
        color: $white;
        display: block;
        letter-spacing: 0.03rem;
        line-height: 2.875rem;
        padding: 1rem 0 1rem 4.5rem;

        > svg {
          color: $white !important;
          margin-right: 1.5625rem;
          opacity: 0.4;
          position: relative;
          top: 0.4rem;
          width: 1.55rem;
        }
      }

      &:hover {
        a {
          color: darken($white, 10%);
        }
      }

      &.active {
        background-color: $dark-main-lighten;
        position: relative;

        &:before {
          background: $blue;
          content: '';
          display: block;
          height: 100%;
          left: 0;
          position: absolute;
          top: 0;
          width: 0.5rem;
        }

        a {
          color: $blue;

          > svg {
            color: $blue !important;
            opacity: 1;
          }
        }
      }
    }
  }
}
