@import "src/theme/font";

.Balances {
  header {
    display: flex;
    margin-bottom: 1rem;

    button {
      margin-left: auto !important;
      position: relative;
      top: -3px;
    }
  }

  .accordion {
    .title {
      display: flex;
      padding: 1.5rem 2rem 1.5rem 3.5rem !important;

      span {
        font-size: 1.5rem;
        font-weight: 500;
        margin-right: 10rem;
      }

      svg {
        margin-left: auto;
      }
    }

    .content {
      padding: 1.9rem 2rem 1.5rem !important;
    }
  }
}
