@import "src/theme/background";
@import "src/theme/colors";
@import "src/theme/font";

.ViewCardModal {
  .content {
    .CardDetails {
      .CopyAll {
        margin: 30px 0 7px;
        padding: 0 15px;
        text-align: right;

        > span {
          color: $link-color;
          cursor: pointer;
          font-weight: 700;
        }
      }

      .Fields {
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        gap: 15px;

        > div {
          background: $dark-background-darken;
          padding: 10px;
          position: relative;
          width: 48%;

          > span {
            color: $dark-lead-color;
            display: block;
            font-weight: 700;
            margin-top: 10px;
            text-transform: uppercase;
          }

          > svg {
            color: $link-color;
            cursor: pointer;
            font-size: 14px;
            position: absolute;
            right: 10px;
            top: 10px;
          }
        }
      }
    }

    .TypePassword {
      align-items: center;
      flex-direction: column;
      display: flex;

      h2 {
        font-size: 1.875rem;
        font-weight: 500 !important;
        margin-top: 0.5rem;
      }

      input[type=password] {
        width: 30rem !important;
      }

      button {
        padding-bottom: 1.2rem;
        padding-top: 1.2rem;
        width: 30rem;
      }

      p {
        color: $light-grey-text;
        font-weight: 300;
      }
    }

    .Lead {
      text-align: center;
      width: 50%;
    }

    .Additional {
      font-size: 1.125rem;
      margin: 2rem 0;

      > span {
        color: $light-grey-text;
        cursor: pointer;
      }
    }
  }
}
