.SettingsForm {
  display: flex;
  gap: 20px;

  > div {
    &:first-child {
      flex: 2;
    }

    flex: 3;
  }

  .field {
    margin-bottom: 20px !important;

    > label {
      font-size: 14px !important;
      font-weight: 300 !important;
    }

    .ToggleText {
      > span {
        display: inline-block;
        font-size: 12px;
        position: relative;
        top: -5px;
        width: 300px;
      }
    }
  }
}
